<template>
    <div class="circleCharts">
        <div id="circleCharts" ref="circleCharts"></div>
    </div>
</template>

<script>
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp =
    {
      tooltip: {
        trigger: 'item'
      },
      // legend: {
      //   top: '5%',
      //   left: 'center'
      // },
      series: [
        {
          name: 'Access From',
          type: 'pie',
          radius: ['40%', '70%'],
          avoidLabelOverlap: false,
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          label: {
            show: false,
            position: 'center'
          },
          emphasis: {
            label: {
              show: true,
              fontSize: '40',
              fontWeight: 'bold'
            }
          },
          labelLine: {
            show: false
          },
          data: [
            { value: 1048, name: 'Search Engine' },
            { value: 735, name: 'Direct' },
            { value: 580, name: 'Email' },
            { value: 484, name: 'Union Ads' },
            { value: 300, name: 'Video Ads' }
          ]
        }
      ]
    };

  export default {
    name: "circleCharts",
    props: {
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      xData:{
        /*** * xData:['一月',二月]*/
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData: {
        /*** * seriesData:[{
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },]*/
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        }
      },

    },
    watch: {

    },
    data() {
      return {
        option: optionTemp,
      }


    },
    mounted() {
      this.init();
    },
    methods: {
      dataSet(){
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        if (this.seriesData.length>0){
          this.option.legend.data = []
          this.seriesData.forEach(item=>{
            this.option.legend.data.push(item.name)
          })
          this.option.series = this.seriesData
        }
      },
      init() {
        // this.dataSet()
        let mychart = echarts.init(this.$refs.circleCharts, );
        window.addEventListener("resize", mychart.resize);
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.option);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
          // window.console.log(params);
        });

      },
    }

  }
</script>

<style scoped lang="scss">
    .circleCharts {
        height: 100%;
        #circleCharts {
            height: 100%;
            /*width: 97%;*/
            /*height: 99%;*/
        }
    }
</style>